import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { Typography, Paper, Button } from '@material-ui/core';
import Typist from 'react-typist';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import headshot from './headshot.png'

const height = 50;

const styles = (theme) => ({
    paper: {
        height: `${height}em`,
        backgroundColor: "#fffbeb",
    },
    heading: {
        paddingTop: "2em",
    },
    headshot: {
        display: "block",
        width: "300px",
        margin: "auto",
        paddingTop: "5em"
    },
    downArrow: {
        position: "absolute",
        top: `${height}em`,
        width: "100%",
    },
});

function Title(props) {
    const { classes } = props;

    const scrollDown = () => {
        window.scroll({ top: height * 12.5, behavior: 'smooth' })
    }
    return (
        <div>
            <Paper
                className={classes.paper}
                elevation={0}
            >
                <img alt="headshot" src={headshot} className={classes.headshot} onClick={() => scrollDown()} />
                <Typography
                    variant="h3"
                    align="center"
                    className={classes.heading}
                >
                    <Typist
                        startDelay={800}
                        cursor={{ hideWhenDone: true }}
                        avgTypingDelay={100}
                    >
                        Hello World!
                    </Typist>
                </Typography>
                <Typography
                    variant="h5"
                    align="center"
                    className={classes.heading}
                >
                    <Typist
                        startDelay={3500}
                        cursor={{ hideWhenDone: false, blink: true }}
                        avgTypingDelay={80}
                    >
                        I'm Jerry, and I'm a full-stack developer.
                    </Typist>
                </Typography>
                <div className={classes.downArrow}>
                    <Button
                        style={{ display: "block", margin: "auto", }}
                        disableRipple={true}
                        onClick={() => scrollDown()} >
                        <ExpandMoreIcon />
                    </Button>
                </div>
            </Paper>
        </div >
    );
}

Title.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Title);
