import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from './AppBar';
import Toolbar, { styles as toolbarStyles } from './Toolbar';
import { Button, Link, Tooltip } from '@material-ui/core';
import Drawer from "./Drawer"
import DescriptionIcon from '@material-ui/icons/Description';


const styles = (theme) => ({
  title: {
    fontSize: 24,
  },
  placeholder: toolbarStyles(theme).root,
  toolbar: {
    justifyContent: 'space-between',
  },
  left: {
    flex: 1,
  },
  leftLinkActive: {
    color: theme.palette.common.white,
  },
  right: {
    flex: 1,
    display: 'flex',
    justifyContent: 'flex-end',
  },
  rightLink: {
    fontSize: 16,
    color: theme.palette.common.white,
    marginLeft: theme.spacing(3),
  },
  linkSecondary: {
    color: theme.palette.secondary.main,
  },
  // Styling for Even vs Odd Pages:
  oddPage: {
    backgroundColor: "#fff",
  },
  evenPage: {
    backgroundColor: "#fffbf2",
  },
});

function AppAppBar(props) {
  const { classes } = props;
  const components = [];

  const displayComponents = props.children.map((item, index) => {
    const ref = React.createRef();
    components.push({ item: item, ref: ref });

    var cls = (index) => {
      if (index % 2 === 0) {
        return classes.evenPage;
      } else {
        return classes.oddPage;
      }
    };
    return <div ref={ref} key={item.key} className={cls(index)}>{item}</div>
  });


  return (
    <div>
      <AppBar position="fixed">
        <Toolbar className={classes.toolbar}>
          <div className={classes.left} >
            <Drawer components={components} />
          </div>
          <Button
            underline="none"
            color="inherit"
            className={classes.title}
            onClick={() => window.scroll({ top: 0, behavior: 'smooth' })}
          >
            {'JERRY HALL'}
          </Button>
          <div className={classes.right} >
            <Tooltip title="Note: Opens in new tab">
              <Button
                color="inherit"
                onClick={() => window.open("https://docs.google.com/document/d/1WcJPhMUdPLqkJ-uXXCgEP3O8KWsQXgOB_E4KWLPcMgU/edit#heading=h.ocvpswguxa6m", "_blank")}
              >
                <DescriptionIcon/>&nbsp;Resume
            </Button>
            </Tooltip>
          </div>
        </Toolbar>
      </AppBar>
      <div className={classes.placeholder} />
      {displayComponents}
    </div>
  );
}

AppAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AppAppBar);
