const styles = () => ({
    root: {
        width: "55px",
        height: "4px",
        margin: "8px auto 0",
        display: "block",
        backgroundColor: "#ff3366",
    }
});

function Underline(props) {
    const { classes } = props;
    return (
        <div className={classes.root}>
        </div >
    );
}

Underline.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Underline);
